
import VaildateForm from '@/components/VaildateForm.vue'
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import VaildateInput, { RulesProp } from '../components/VaildateInput.vue'
import createMessage from '../components/createMessage'
import axios from 'axios'

export default defineComponent({
  name: 'regist',
  components: {
    VaildateInput,
    VaildateForm
  },
  setup () {
    const router = useRouter()
    const nameRule:RulesProp = [
      { type: 'name', message: '4' },
      { type: 'required', message: '' }
    ]
    const passwordRule:RulesProp = [
      { type: 'password', message: '6' },
      { type: 'required', message: '' }
    ]
    const nameval = ref('')
    const passwordval = ref('')
    const onSgin = (res:boolean) => {
      if (res) {
        axios.post('/users', {
          name: nameval.value,
          password: passwordval.value
        }).then(res => {
          if (res) {
            createMessage('', 'success')
            setTimeout(() => {
              router.push('/login')
            }, 2000)
          }
        })
      }
    }
    return {
      nameval,
      passwordval,
      nameRule,
      passwordRule,
      onSgin
    }
  }
})
